import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const store = new Vuex.Store({
	state: {
		socketUrl: "ws://192.168.0.64:8888/bluetoothApplets/websocket",
		socketTask: null, // uni.connectSocket() 
		userInfo: null, // 
		identity: null, // 
		socketMessage: null, // 
		isFlag: false // 
	},
	/**
	 * A B
	 * B A 
	 */
	mutations: {
		// websocket
		WEBSOCKET_INIT(state) {
			let url = state.socketUrl + `/${state.identity.userId}`;
			state.socketTask = uni.connectSocket({
				url: url,
				success(data) {
					console.log("websocket", data)
				}
			});
			
			// , 
			state.socketTask.onOpen(res => {
				console.log('');
			})

			// 
			state.socketTask.onMessage(msg => {
				console.log('', msg.data);
				state.socketMessage = JSON.parse(msg.data);
				console.log(state.socketMessage)
			})

			// 
			state.socketTask.onClose(() => {
				console.log('onClose');
				//
				this.commit('WEBSOCKET_INIT')
			})
			state.socketTask.onError(() => {
				console.log('onError')
			})

		},

		// 
		WEBSOCKET_SEND(state, p) {
			state.socketTask && state.socketTask.send && state.socketTask.send({
				data: JSON.stringify(p),
				success: (res) => {
					console.log('', res)
				}
			});
		},

		// 
		SET_USER_INFO(state, data) {
			state.userInfo = data; // vuex
			uni.setStorageSync('userInfo', data); // 
		},

		// 
		SET_USER_INFO(state, data) {
			state.identity = data; // vuex
			uni.setStorageSync('identity', data); // 
		},

		// 
		SET_ISFLAG(state, data) {
			state.isFlag = data;
		}
	},

	actions: {
		WEBSOCKET_INIT({
			commit
		}) {
			commit('WEBSOCKET_INIT')
		},
		SET_USER_INFO({
			commit
		}, info) {
			commit('SET_USER_INFO', info)
		},
		SET_IDENTITY({
			commit
		}, info) {
			commit('SET_USER_INFO', info)
		},

		WEBSOCKET_SEND({
			commit
		}, p) {
			commit('WEBSOCKET_SEND', p)
		}
	}
})

// 
export default store;
